* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1 {
  margin: 2rem;
}

body {
  background-color: black;
  color: white;
  font-family: 'Roboto', sans-serif;
  display: flex;
  justify-content: center;
  min-height: 100vh;
  padding: 10px
}

.board {
  border: 1px solid #fff;
  background-color: #ff652f;
  width: 500px;
  height: 500px;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.squares {
  border-radius: 25px;
  text-align: center;
  font-size: 75px;
  flex-direction: wrap;
  box-sizing: border-box;
  width: 120px;
  height: 120px;
}

.squares:hover {
  background-color: #2980b9;
}

.squares .X {
  color: red;
}

.O {
  color: green;
}
 
h3 {
  padding: 1rem;
}

.info {
  display: flex;
  justify-content: space-between;
}

info button {
  background: #272727;
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0.5rem;
  margin: 0.5rem 1rem;
}

.game {
  display: column;
  justify-direction: center;
  flex-direction: column;
  align-items: center;
  height: 80vh;
}

li {
  list-style: none;
}